/**
 * @description import react
 */
import React from "react";

/**
 * @description material ui
 */
import { Container } from "@mui/material";

/**
 * @description styles components
 */

import { ItemNav } from "./app.style.js";

/**
 * @description react router
 */

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink,
} from "react-router-dom";

/**
 * @description components para el nav
 */

import Inicio from "./components/Inicio";
import ConsumingAPI from "./components/Pokemones.jsx";
import Constacto from "./components/Constacto.jsx";


function App() {
  return (
    <Container>
      <Router>

        <ItemNav to="/" activeClassName="active">
          Home
        </ItemNav>
        <ItemNav to="/ConsumingAPI" activeClassName="active">
          Pokemones
        </ItemNav>
        <ItemNav to="/Constacto" activeClassName="active">
          Contacto
        </ItemNav>
        <hr />

        <Switch>
          <Router path="/Constacto">
            <Constacto />
          </Router>
        </Switch>

        <Switch>
          <Router path="/ConsumingAPI">
            <ConsumingAPI />
          </Router>
        </Switch>

        <Switch>
          <Router path="/" exact>
            <Inicio/>
          </Router>
        </Switch>

      </Router>
    </Container>
  );
}

export default App;
